import React, {useEffect, useState} from "react";
import {Modal} from "../../../common/Modal/Modal";

type ScrollUpModalPropsType = {}

export const ScrollUpModal: React.FC<ScrollUpModalPropsType> = React.memo(({}) => {
    const [show, setShow] = useState(false)
    let scrollSpeed = 10 //   : 50,  10  ,     500 ()

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)
    }, [])

    const handleScroll = () => { //  
        if (window.pageYOffset > 400) {
            setShow(true)
        } else {
            setShow(false)
        }
    }

    const scroll = () => {
        const singleScrollLength = window.pageYOffset / scrollSpeed //,   1  ()
        let lastScrollPosition = window.pageYOffset //       ()

        const timer = setInterval(() => {
            if (lastScrollPosition < window.pageYOffset) { // ,        
                clearInterval(timer)
            } else {
                lastScrollPosition = window.pageYOffset //     
            }

            window.scroll(0, lastScrollPosition - singleScrollLength) //  1  
            if (window.pageYOffset === 0) clearInterval(timer) // ,     
        }, 50)
    }

    return <Modal enableBackground={false} modalHeightPx={50} modalWidthPx={50} show={show}
                  modalOnClick={scroll} modalStyle={{
        top: '85vh',
        left: '60px',
        border: '1px solid #d9d9d9',
        borderRadius: '2px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#fafafa',
        boxShadow: '5px 5px 5px grey',
        opacity: '0.5',
        cursor: 'pointer'
    }}>
        <h2></h2>
    </Modal>
})
